
@font-face{
  font-family: 'code-bold';
  src: url('../../assets/fonts/code-bold.OTF');
}
@font-face{
  font-family: 'echomotors';
  src: url('../../assets/fonts/echomotors-demo.OTF');
}
@font-face{
  font-family: 'redounded-elegance';
  src: url('../../assets/fonts/rounded-elegance.TTF');;
}
@font-face{
  font-family: 'costure';
  src: url('../../assets/fonts/costure-bold.OTF');;
}

$code-bold: 'code-bold', serif;
$echomotos-demo: 'echomotos', serif;
$rounded-elegance: 'redounded-elegance', serif;
$costure: 'costure', serif;
$azul-footer: #0157a8;

.codeBold{
  font-family: $code-bold;
}
.costure{
  font-family: $costure;
}
.roundedElegance{
  font-family: $rounded-elegance;
}
.echomotos{
  font-family: $echomotos-demo;
}
body{
  height: calc(100vh - 80px);
}
.navbar{
  height: 80px;
  box-shadow: 0px 2px 2px 1px #00000038;
  z-index: 99;
  .navbar-brand{
    .logo{
      max-height: 60px;
    }
  }
}
.section-back{
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: 100% 0;
  height: 100vh;
  padding-left: 15px;
  padding-right: 15px;
  z-index: 99;
  .sticker{
    max-width: 100px;
  }
}
.carousel-caption{
  right: 25px !important;
  bottom: 5% !important;
  text-align: right !important;
  h1{
    font-size: 4rem !important;
    font-family: $rounded-elegance;
    strong{
      text-transform: uppercase;
      font-family: $echomotos-demo !important;
      font-weight: 700;
    }
  }
}
.contenIcons{
  display: flex;
  flex: 1;
  justify-content: space-around;
}
.contentIcon{
  text-align: center;
  img{
    margin: 0 auto;
  }
  .title{
    text-align: center;
    color: white;
  }
}
.text-clients{
  text-align: right;
  margin-top: 2%;
  h2{
    font-size: 2rem;
    color: white;
    span.codeBold{
      font-weight: 800;
      letter-spacing: 10px;
    }
    span.costure{
      font-size: 4rem;
      font-weight: bold;
    }
  }
}
.services{
  .row{
    height: 350px;
    .item{
      &:nth-child(1){
        margin-top: -30px;
      }
      &:nth-child(3){
        margin-bottom: -50px;
      }
      p{
        text-align: center;
        font-size: 1.8rem;
        color: #ffffff;
        text-transform: uppercase;
        font-family: $code-bold;
      }
    }
  }
}
.benefits{
  position: relative;
  // height: 20vh;
  h2{
    color: #7f7f7f;
    text-align: left;
    font-size: 3rem;
    text-transform: capitalize;
    padding-left: 15px;
    margin-bottom: 0px;
    margin-top: -150px;
    text-transform: uppercase;
    font-weight: 700;
  }
  p{
    font-size: 1rem;
    color: #7f7f7f;
    padding-left: 15px;
  }
  img.background{
    position: absolute;
    top: -150px;
    z-index: -1;
    max-height: calc(100% + 150px);
    width: 100%;
  }
  .boton{
    max-height: 50px;
  }
  .beneficio{
    color: $azul-footer;
    font-weight: 600;
    font-size: 2rem;
  }
  .content-benefits{
    padding: 15px;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    span{
      margin-left: 10px;
    }
  }
}

footer{
  background-color: $azul-footer;
  color: #ffffff;
  // height: 100px;
  .row{
    div{
      padding: 15px;
      display: flex;
      justify-content: center;
      p{
        font-weight: 700;
        font-family: $code-bold;
        font-size: .9rem;
        margin: 0;
      }
    }
  }
}
